(function () {
  'use strict';

  angular.module('app').component('home', {
    controller: HomeController,
    controllerAs: '$ctrl',
    templateUrl: 'app/home/home.view.html'
  });

  function HomeController() {
    var _this = this;

    this.HEADER = 'Client permissions for modules';
    this.LIST_PERMISSIONS_HEADER = 'Modules for clients';
    this.clients = [{
      id: 1,
      name: 'HSBC'
    }, {
      id: 2,
      name: 'Citibank'
    }, {
      id: 3,
      name: 'Wells'
    }];
    this.modules = [{
      id: 1,
      name: "Analytics",
      description: 'Analytics Module'
    }, {
      id: 2,
      name: "Reporting",
      description: 'Reporting Module'
    }, {
      id: 3,
      name: 'Downloads',
      description: 'Downloads Module'
    }, {
      id: 4,
      name: 'Home',
      description: 'Home Module'
    }];
    this.clientModulePermissions = [{
      clientid: 1,
      moduleid: 4
    }, {
      clientid: 1,
      moduleid: 1
    }, {
      clientid: 1,
      moduleid: 2
    }, {
      clientid: 1,
      moduleid: 3
    }, {
      clientid: 2,
      moduleid: 4
    }, {
      clientid: 2,
      moduleid: 1
    }, {
      clientid: 2,
      moduleid: 2
    }, {
      clientid: 3,
      moduleid: 4
    }, {
      clientid: 3,
      moduleid: 3
    }];

    this.getSelectedClientText = function () {
      return _this.selectedClient ? _this.selectedClient.name : 'Please select a client';
    };

    this.changedClient = function () {
      return _this.modulesForClient = _this.clientModulePermissions.filter(function (permission) {
        return permission.clientid === _this.selectedClient.id;
      }).map(function (permission) {
        return _this.modules.filter(function (module) {
          return module.id === permission.moduleid;
        })[0];
      });
    };

    this.changedModule = function () {
      return _this.clientModulePermissions = [].concat(_toConsumableArray(_this.clientModulePermissions.filter(function (permission) {
        return permission.clientid !== _this.selectedClient.id;
      })), _toConsumableArray(_this.modulesForClient.map(function (moduleForClient) {
        return {
          clientid: _this.selectedClient.id,
          moduleid: moduleForClient.id
        };
      })));
    };

    this.getPermissionDescription = function (permission) {
      var permissionClient = _this.clients.find(function (client) {
        return client.id === permission.clientid;
      });

      var permissionModule = _this.modules.find(function (module) {
        return module.id === permission.moduleid;
      });

      return "Client " + permissionClient.name + " - Module " + permissionModule.name + " (" + permissionModule.description + ")";
    };
  }
})();


